/**
 * 下载工具函数
 */

/**
 * 获取完整的下载URL
 * @param {string} downloadUrl - 相对下载URL
 * @returns {string} - 完整的下载URL
 */
export const getDownloadUrl = (downloadUrl) => {
  if (!downloadUrl) return ''
  
  // 如果已经是完整URL，直接返回
  if (downloadUrl.startsWith('http://') || downloadUrl.startsWith('https://')) {
    return downloadUrl
  }
  
  // 生产环境：使用配置文件中的API地址
  if (process.env.NODE_ENV === 'production') {
    if (window.APP_CONFIG && window.APP_CONFIG.API_FULL_URL) {
      return `${window.APP_CONFIG.API_FULL_URL}${downloadUrl}`
    }
    return `https://api.openai2025.com${downloadUrl}`
  }
  
  // 开发环境：使用后端API服务器地址
  return `http://localhost:5162${downloadUrl}`
}

/**
 * 下载文件
 * @param {string} downloadUrl - 下载URL
 * @param {string} filename - 文件名
 */
export const downloadFile = (downloadUrl, filename = '') => {
  if (!downloadUrl) {
    console.error('Download URL is required')
    return
  }
  
  const fullUrl = getDownloadUrl(downloadUrl)
  
  try {
    const link = document.createElement('a')
    link.href = fullUrl
    link.target = '_blank'
    
    if (filename) {
      link.download = filename
    }
    
    // 添加到DOM并触发点击
    document.body.appendChild(link)
    link.click()
    document.body.removeChild(link)
    
    console.log('Download started:', fullUrl)
  } catch (error) {
    console.error('Download failed:', error)
    throw error
  }
}

/**
 * 获取API基础URL（用于调试）
 * @returns {string} - API基础URL
 */
export const getApiBaseUrl = () => {
  if (process.env.NODE_ENV === 'production') {
    if (window.APP_CONFIG && window.APP_CONFIG.API_FULL_URL) {
      return window.APP_CONFIG.API_FULL_URL
    }
    return 'https://api.openai2025.com'
  }
  return window.location.origin
}
